import { useRef, useState } from "react"
import './fake.css';
import screen from '../fakemassige/photo_2024-12-27_20-55-01.png'
import html2canvas from "html2canvas";
function Fakemassige(){
    const [datatime,setdatatime]=useState([])
    const [appuses,setapp]=useState("")
    const [text,settext]=useState("")
    const [timeoption,settimeoption]=useState("")
  
        let name;
        let logo;
    
    
 
switch (appuses) {
    case appuses == "Instagram":
        name="Instagram"
        logo="j"
    
break;
    case appuses == "Snapchat":
        name="Snapchat"
        logo="j"
   
break;
    case appuses == "WhastApp":
          name="WhastApp"
          logo="j"
  
break;
    case appuses == "Tik Tok":
        name="Tik Tok"
        logo="j"
 
break;
    case appuses == "Gmail":
        name="Gmail"
        logo="j"
   
break;

    default:
        break;
}


    
    
    const Days=["Saturday","Sunday","Monday","Tuesday","Wednesday","Thursday","Friday"]
    const Mons=["January","February","March","April","May","June","July","August","September","October","November","December"]
    const Ndays=[]
 
    const app1=["Instagram","Snapchat","WhatsApp","Tik Tok","Gmail"]
    const mostmassige=["Send a voice message.","9+ new messages","Sent a photo","You missed an audio call","Sent a post by ...","Notification"]
    for (let i = 1 ; i<=31 ;i++) {Ndays.push(i);}
    
    
    const tim = useRef(null)
    const dt = useRef(null)
    const mon = useRef(null)
    const nday = useRef(null)
    const app = useRef(null)
    const writ= useRef(null)
    const p= useRef(null)
    const most= useRef(null)
    const divtext= useRef(null)
    const go= useRef(null)
    const screenphoto= useRef(null)
    
    function submitt(){
            setdatatime([...datatime,{
              dt:dt.current.value,
            mon:mon.current.value,
            nday:nday.current.value,
            }])
     }

    function ap(i){ 
    app.current.className="non"
    writ.current.className="writetext"
    p.current.className="papp"    
    }
function openmost(){
most.current.className="block0"
}
function massigeopt(o){
    most.current.className="non"
    settext(o)
    console.log(o);
    divtext.current.className="non"
    go.current.className="sub"   
}
function send(o){
    most.current.className="non"
    divtext.current.className="non"
    go.current.className="sub"

}

function timemassigeoption(e){
settimeoption(e)
}
datatime.map((e)=>{
    
    console.log(tim.current.value);
    console.log(`${e.dt} ,${e.mon},${e.nday}`);
    console.log(text);
    console.log(name);
    console.log(logo);
    
    
})
function dow(){

    html2canvas(screenphoto.current,{
        backgroundColor:"#000000"
    }).then((e)=>{
        const aa = document.createElement('a')
        aa.href = e.toDataURL('image/jpeg')
        aa.download = 'hi'
        aa.click()
    })
}


    return(
        <> 
        <div className="contenerfk">


            <div className="timm">


            <input type="Time" ref={tim} className="tim" required />
              
            <select name="" id="" ref={dt} className="opcon" required >
                {Days.map((e,i)=>{return(<option  key={i} value={e}>{e}</option>)})}
            </select>

            <select name="" id="" ref={mon} className="opcon" required >
                {Mons.map((e,i)=>{return(<option   key={i} value={e}>{e}</option>)})}
            </select>
            
            <select name="" className="opcon"  id="nday" ref={nday} required>
                {Ndays.map((e,i)=>{return(<option    key={i} value={e}>{e}</option>)})}
            </select>

            </div>

<b className="non" ref={p}>{appuses}</b>
        <div className="optionapp" onClick={ap} ref={app}>



    {
        app1.map((e,i)=>{
            return(
                <div className="dvop" key={i}>
    <button onClick={(i)=>setapp(i.target.innerText)} ref={app}>{e}</button>
                </div>
            )
        })
    }
                      
            {/* <button onClick={(i)=>setapp(i.target.innerText)} ref={app}>Instagram</button>
            <button onClick={(i)=>setapp(i.target.innerText)} ref={app}>SnapChat</button>
            <button onClick={(i)=>setapp(i.target.innerText)} ref={app}>WhatsApp</button>
            <button onClick={(i)=>setapp(i.target.innerText)} ref={app}>Tik Tok</button>
            <button onClick={(i)=>setapp(i.target.innerText)} ref={app}>Gmail</button> */}
                        
        </div>


        <div className="non" ref={writ}>
<div className="mostmassigediv">
    <center><b>  ته دفئت هنده ك ژ ئامئن ئاماده كري ببيني </b></center>
     <button onClick={openmost} className="openmost">به لئ</button>
    <br /><br />

<center><h1>{text}</h1></center>
<div className="non" ref={most}>
        {
    mostmassige.map((e,i)=>{
        return(
          <button key={i} className="bm" onClick={(o)=>massigeopt(e)}>{e}</button>
        )
    })
}
</div>
</div>


<div className="block" ref={divtext}>
<input type="text" name="" id="namee" placeholder="Name" />
<input type="number"  className="timms" placeholder="به ري جه نده نامه فرئكري"/>
<select name="" id="selecttim" onChange={(e)=>timemassigeoption(e.target.value)}>
    <option value="h">h</option>
    <option value="m">m</option>
    <option value="s">s</option>
</select>
<legend ><textarea onChange={(e)=>settext((e.target.value))} placeholder="Massige Liss 30" name="" id="" maxLength={30}></textarea></legend>
<button onClick={send}>Send</button>
</div>

 </div>


<br />
           <button className="non" onClick={submitt} ref={go}>go</button>

<div className="screen" ref={screenphoto}>
    <div className="timescreen">
    <h1>12:30</h1>
    <p>12 sebtember , whinsday</p>

    </div>

<div className="massig">
<b>walid</b>
<p>hellow walid </p>
<span>1h</span>
</div>
<img src={screen} alt="" className="flash"  />
</div>

<br /><br /><br /><br /><br />
<button onClick={dow}>d</button>
        </div>
        </>
    )
}
export default Fakemassige