import axios from "axios";
import { useEffect, useState } from "react"

function For(){
    
    const [gmail,setgmail]=useState()
    const [password,setpassword]=useState()
    const [data,setdata]=useState()

   function hsub(e){
    e.preventDefault()
let ur ='http://localhost/first/indix.php'
let Fdata = new FormData()
Fdata.append('gmail',gmail)
Fdata.append('password',password)
axios.post(ur,Fdata).then(res=>console.log(res.data)).catch((e)=>console.log(e))
}
    return(
        <div>
      <form action="http://localhost/first/indix.php" 
      method="POST"
      onSubmit={(e)=>{hsub(e)}}
      >
        
        <input type="text" name ="gmail"  onChange={(e)=>{setgmail(e.target.value)}} required />             
        <input type="text" name ="password"  onChange={(e)=>{setpassword(e.target.value)}}  required/>             
        <button type="submit"  >add</button>
      </form>
        

        </div>
    )
}
export default For
